export const validateNonEmptyLength = (field) => {
    if (field.length === 0) return "    ";
}

export const validateMaxLength = (field, size) => {
    if (field.toString().length > size) return `    ${size}  `;
}

export const validateMinLength = (field, size) => {
    if (field.length < size) return `    ${size}    `;
}

export const validatePhoneNumber = (phone_number) => {
    if (/[a-zA-Z]/g.test(phone_number) || (phone_number.length !== 10)) return "    ";
}

export const validatePersonName = (name) => {
    if (/\d/.test(name) || (name.length < 2)) return "    2        ";
}

export const validatePassword = (password) => {
    if (!(/[a-zA-Z]/g.test(password) && /\d/.test(password)) || (password.length <= 8)) return "    8  ,      ";
}